import { createGlobalStyle } from "styled-components";
import Router from "./Router";
import { HelmetProvider } from "react-helmet-async";
import { ThemeProvider } from 'styled-components';
import {darktheme, lighttheme} from "./theme";
import { useState } from "react";

const GlobalStyle = createGlobalStyle`
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&family=Source+Sans+Pro:wght@300;400&display=swap');
  body {
    
    font-family: 'Source Sans Pro', sans-serif;
    background-color:${props => props.theme.bgColor};
    color:${props => props.theme.textColor};
  }
  * {
    box-sizing : border-box;
  }
  a {
    text-decoration: none;
    color:inherit;
  }
  html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
`;
  


function App() {

  const [useLightTheme, setUseLightTheme] = useState(true);




  return (
    <ThemeProvider theme={ useLightTheme ? lighttheme : darktheme }>
      <GlobalStyle />
      <HelmetProvider>
      <button onClick={() => setUseLightTheme( !useLightTheme )}>Change Theme</button>
      <Router />
      </HelmetProvider>
    </ThemeProvider>
  );


}

export default App;
